  <div
    class="title"

  >
    <div id="wrapper">
      <vue-particles
        color="#587792"
        :particleOpacity="1.0"
        :particlesNumber="38"
        shapeType="circle"
        :particleSize="9"
        linesColor="8db1ab"
        :linesWidth="2"
        :lineLinked="true"
        :lineOpacity="1.0"
        :linesDistance="170"
        :moveSpeed="0.75"
        :hoverEffect="true"
        hoverMode="grab"
        :clickEffect="true"
        clickMode="push"
      ></vue-particles>
      <div id="wrapper">
        <div id="text">
          <div id="first">Vishnu</div>
          <div id="last">Rengaraj</div>
          <div id="subtitle">{{ whois[0] }}</div>
        </div>
      </div>

      <router-link :to="{ name: 'Home2About' }">
        <div id="open-about-text">learn more</div>
        <div id="open-about-button">
          <img src="../assets/chevron_down.png" />
        </div>
      </router-link>

      
    </div>
    <div id='contents'>
    <About />
    </div>
