<script>
// v-on:wheel.passive="wheelson"
// v-touch:swipe.top="swipeUpHandler"
// import Resume from "./Resume.vue";
// import Project from "./Project.vue";
// var VueScrollTo = require("vue-scrollto");
import About from "./About.vue";

export default {
  name: "title",
  components: {
    About,
  },

  data() {
    return {
      scrolled: false,
      // isActiveProject: false,
      whois: [
        "Mechanical Engineer",
        "Computer Scientist",
        "Graduate Student",
        "Designer",
        "Stress Analyst",
        "Banana Lover",
      ],
    };
  },
  mounted() {
    window.setInterval(() => {
      this.pollPerson();
    }, 2700);
  },
  methods: {
    clear: function () {
      this.scrolled = false;
      // this.isActiveResume = false;
      // this.isActiveProject = false;
    },

    swipeUpHandler: function () {
      this.$router.push({ name: "Home2About" });
    },

    wheelson: function (event) {
      if (event.deltaY > 0 && this.scrolled == false) {
        this.scrolled = true;
        // VueScrollTo.scrollTo("#subtitle");
        // console.log(this.scrolled);
        this.$router.push({ name: "Home2About" });
      }
    },

    pollPerson() {
      const first = this.whois.shift();
      this.whois = this.whois.concat(first);
    },
  },
};
</script>

